import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import Sdata from './Sdata';
import  Card from './Cards';

function ncard(val) {
  return (
    <Card
      key={val.id}
      imgsrc={val.imgsrc}
      title={val.title}
      sname={val.sname}
      link={val.link}
    />
  );
}

ReactDOM.render(
  <>
    <span><h1 className="heading_style"> Welcome to the Most Watched Netflix Series</h1></span>
    <hr></hr>
    {Sdata.map(ncard)}
  </>,
  document.getElementById("root")
);
